.nav {
  box-shadow: 0 2px 2px rgb(180, 179, 179);
  margin-bottom: 20px;
}

.list {
  display: flex;
  justify-content: flex-start;
  list-style: none;
  margin: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

.listItem {
  margin: 5px 20px;
  list-style: none;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: bold;
}

.link {
  color: rgb(41, 27, 24);
  padding: 0px 20px;
  text-decoration: none;
}

.activeLink {
  color: rgb(27, 167, 39);
}
