.imageBox {
  min-width: 300px;
  height: 450px;
  background-image: url('../../images/depotemplate-popcorn-soda.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 30px;
}

.movieCard {
  display: flex;
  flex-direction: column;
}

.mainInfo {
  display: flex;
  padding-bottom: 20px;
  box-shadow: 0 2px 2px rgb(180, 179, 179);
}

.movieName {
  font-size: 26px;
}
